import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ClarityModule } from 'clarity-angular';
import { FormsModule } from '@angular/forms';
import { AdminComponent } from './admin.component';
import { AdminRoutes } from './admin.routes';
import { RouterModule } from '@angular/router';
import { ExperienceComponent } from './experience/experience.component';
import { EntertainmentComponent } from './entertainment/entertainment.component';
import { GrubComponent } from './grub/grub.component';
import { TwentyOneComponent } from './twentyone/twentyone.component';
import { SightsComponent } from './sights/sights.component';
import { OrganizationsComponent } from './organizations/organizations.component';
import { CampusComponent } from './campus/campus.component';
import { BusinessesComponent } from './businesses/businesses.component';
import { SharedModule } from '../shared/shared.module';


@NgModule({
  imports: [
    CommonModule, RouterModule.forChild(AdminRoutes), ClarityModule.forRoot(), FormsModule, SharedModule.forRoot(),
  ],
  declarations: [AdminComponent, ExperienceComponent, EntertainmentComponent, GrubComponent, TwentyOneComponent, SightsComponent, OrganizationsComponent, CampusComponent, BusinessesComponent]
})
export class AdminModule { }


