import { Component, OnInit } from '@angular/core';
import { FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { AppState, DataState } from '../../store/state';
import { AppActions } from '../../store/actions/appActions';
import { Router } from '@angular/router';

@Component({
  selector: 'app-organizations',
  templateUrl: './organizations.component.html',
  styleUrls: ['./organizations.component.scss']
})
export class OrganizationsComponent implements OnInit {

  organizations: FirebaseListObservable<any[]>;
  category: string = 'Campus Organization';
  query: string = '';
  school: string;
  constructor(private store: Store<AppState>, private router: Router) { }

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state;
    }).subscribe((state: AppState) => {
      this.organizations = state.data.campusEntities;
      this.school = state.view.school;
    });
  }

  searchUpdate(value: string): void {
  	this.query = value.toLowerCase();
  }

  edit(org: any): void {
    org.school = this.school;
    this.store.dispatch({type: AppActions.SET_VIEW_ORGANIZATION, payload: org});
    this.router.navigate(['/view/organization']);
  }
}


