import { Component, OnInit } from '@angular/core';
import { FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { AppState, DataState } from '../../store/state';
import { AppActions } from '../../store/actions/appActions';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sights',
  templateUrl: './sights.component.html',
  styleUrls: ['./sights.component.scss']
})
export class SightsComponent implements OnInit {

  sights: FirebaseListObservable<any[]>;
  city: string;
  query: string = '';
  constructor(private store: Store<AppState>, private router: Router) { }

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state;
    }).subscribe((state: AppState) => {
        this.sights = state.data.sightEntities;
        if(this.city != state.view.city){
        	this.city = state.view.city;
        	this.store.dispatch({type: AppActions.GET_SIGHT_ENTITIES, payload: this.city});
        }
    });
  }

  searchUpdate(value: string): void {
    this.query = value.toLowerCase();
  }

  edit(sight: any): void {
    sight.city = this.city;
    this.store.dispatch({type: AppActions.SET_VIEW_SIGHT, payload: sight});
    this.router.navigate(['/view/sight']);
  }
}


